.finish-quiz {
    padding: 20px;
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
}

.finish-quiz-ul {
    list-style: none;
    padding: 0;
    margin: 0;
}



.finish-error {
    color: rgba(233, 8, 27)
}

.finish-complete {
    color: rgba(11, 233, 11)
}
