.QuizList {
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(90deg, #fd8355 0%, #f0576c 37%, #f79cbd 100%);
}

.QuizList h1 {
    color: #fff ;
}

.QuizList ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.QuizList li {
    margin-bottom: 15px;
 }

.QuizList li a{
   color: #fff;
   text-decoration: none;
   font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.QuizList li a:hover{
    color: #363f54;
 }