.button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 7px;
    border: none;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    color: black;
    cursor:  pointer;
}


/* .button:hover {
    background: rgb(5, 45, 175);
    transition: background 1.2s ease-in-out;
    cursor: pointer;
} */

.button:active {
   box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);
}

.disabled {
    background: gray;
    color: black;
    cursor: not-allowed;
}

.error {
    background: rgba(233, 8, 27, 0.8);
}

.success {
    background: rgba(11, 233, 11, 0.8);
}

.primary {
    background: #2884f6;
    color: #fff;
}