.drawer{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 300px;
    padding: 20px 10px;
    box-sizing: border-box;
    background: #fff;
    transform: translateX(0px);
    transition: transform .22s ease-in;
    z-index: 90;
    border-radius: 0 30px 0 0;
}

.close {
    transform: translateX(-300px);
}

.drawer ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.drawer li {
    margin-bottom: 15px;
}

.drawer li a {
    color: #363d54;
    font-size: 20px;
    text-decoration: none;
    position: relative;
    padding: 0 20px 10px 20px;
}



.drawer a:hover, .a:active {
    opacity: .7;
}