.answeritem-li {
    border: 1px solid black;
    margin-bottom: 10px;
    border-radius: 5px;
    padding: 0 5px;
}

.answeritem-li:hover {
    background: rgb(172, 165, 165, 0.5);
    transition: background .3s ease-in-out;
    cursor: pointer;
}

li.success {
    background: rgba(11, 233, 11, 0.5) !important;
}

li.error {
    background: rgba(233, 8, 27, 0.5) !important;
}